@import "../../helpers/variables";
@import "../../helpers/mixins";

.works {
  margin: $level-2 0;
  @include adaptive(1366) {
    margin: $level-3 0;
  }
  &__wrapper {
    @include adaptive(768) {
      display: flex;
      flex-direction: column;
    }
  }
  &__controls {
    display: flex;
    position: absolute;
    right: 0;
    top: $level-5;
    @include adaptive(1024) {
      right: 25px;
    }
    @include adaptive(600) {
      right: 18px;
    }
    @include adaptive(768) {
      order: 2;
      position: relative;
      right: auto;
      height: 36px;
    }
  }
  &__slider {
    margin-top: $level-4;
    @include adaptive(768) {
      order: 3;
      width: 100%;
    }
    &_item {
      display: flex;
      @include adaptive(768) {
        flex-direction: column;
      }

      &.swiper-slide-active{
        .works__slider_image {
          &::before {
            animation: slide 1.5s .6s;
          }

          img {
            animation: slideImage 1.5s .6s;
            transform: translateX(0);
            transition-delay: 1.5s;
          }
        }

        .works__slider_name {
          transform: translateX(0);
          opacity: 1;
          transition-delay: 2s;
        }

        .works__slider_params{
          transform: translateX(0);
          opacity: 1;
          transition-delay: 2.3s;
        }

        .works__slider_text{
          transform: translateX(0);
          opacity: 1;
          transition-delay: 2.6s;
        }

        .works__slider_link{
          transform: translateX(0);
          opacity: 1;
          transition-delay: 2.9s;
        }
      }
    }
    &_image {
      width: calc(50% + 15px);
      flex-shrink: 0;
      display: flex;
      overflow: hidden;
      background: #f2f2f2;
      transition: .6s;
      @include adaptive(1600) {
        width: calc(50% + 10px);
      }
      @include adaptive(1200) {
        width: calc(50% + 5px);
      }
      @include adaptive(1024) {
        width: 50%;
      }
      @include adaptive(768) {
        width: 100%;
        max-height: 400px;
      }
      & img {
        object-fit: cover;
        transform: translateX(-100%);
        transition: .6s;

        @include adaptive(768) {
          height: 100%;
          width: 100%;
          display: block;
        }
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: $orange;
        z-index: 1;
        transform: translateX(-100%);
      }
    }
    &_info {
      display: flex;
      flex-direction: column;
      background: #f2f2f2;
      padding: $level-4 95px;
      width: 100%;
      overflow: hidden;
      @include adaptive(1600) {
        padding: $level-5 80px;
      }
      @include adaptive(1200) {
        padding: $level-5 70px;
      }
      @include adaptive(768) {
        padding: $level-5;
      }
    }
    &_name {
      margin-bottom: $level-3;
      transform: translateX(-120%);
      opacity: 0;
      transition: .6s;
      @include adaptive(1600) {
        margin-bottom: $level-4;
      }
    }
    &_params {
      margin-bottom: $level-4;
      transform: translateX(-120%);
      opacity: 0;
      transition: 1s;
      @include adaptive(1600) {
        margin-bottom: $level-5;
      }
    }
    &_param {
      display: flex;
      padding: $level-8 0;
      border-bottom: 1px solid $gray;
      &:first-child {
        padding-top: 0;
      }
      &:last-child {
        padding-bottom: 0;
        border: none;
      }
      &-name {
        width: 50%;
      }
      &-value {
        width: 50%;
        padding-left: 60px;
        @include adaptive(600) {
          padding-left: 10px;
        }
      }
    }
    &_text {
      margin: $level-4 0;
      transform: translateX(-120%);
      opacity: 0;
      transition: 1s;
      @include adaptive(1600) {
        margin: $level-5 0;
      }
    }
    &_link {
      margin-top: auto;
      transform: translateX(-200%);
      opacity: 0;
      transition: 1s;
      &:hover {
        text-decoration: none;
      }
    }
  }
}

@keyframes slide {
  from {
    transform: translateX(-100%);
    transition-timing-function: ease-out;
  }
  35% {
    transform: translateX(0);
    transition-timing-function: ease-out;
  }
  65% {
    transform: translateX(0);
    transition-timing-function: ease-out;
  }
  to {
    transform: translateX(100%);
    transition-timing-function: ease-out;
  }
}

@keyframes slideImage {
  from {
    transform: translateX(-100%);
    transition-timing-function: ease-out;
  }
  65% {
    transform: translateX(-100%);
    transition-timing-function: ease-out;
  }
  to {
    transform: translateX(0);
    transition-timing-function: ease-out;
  }
}
