.services-detail {
  display: flex;
  margin-top: 48px;
  &__container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: calc(100% - 345px);

    @include adaptive(1600) {
      width: calc(100% - 270px);
    }

    @include adaptive(1024) {
      width: 100%;
    }
  }

  &__top {
    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: -48px;
      left: 0;
      background: #f2f2f2;
      transform: translateX(70%);
    }
    p {
      margin: 0 0 25px;
      max-width: 740px;
      width: 100%;
    }
    & .detail-background-text{
      @include adaptive(1200) {
        font-size: 60px;
      }
    }
  }

  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: $level-4 0 0;

    img {
      width: 100%;
      height: auto;
    }
  }

  &__steps {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    width: 100%;
    margin: $level-5 0 0;
  }

  &__step {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 25%;
    padding-right: 26px;
    margin-bottom: $level-5;

    @include adaptive(768) {
      width: calc(100% / 3);
    }

    @include adaptive(576) {
      width: calc(100% / 2);
      margin-bottom: $level-7;
    }

    @include adaptive(360) {
      width: 100%;
      padding-right: 0;
    }

    &:not(:last-child) {
      &::before {
        content: '';
        position: absolute;
        right: 26px;
        top: 32px;
        height: 1px;
        width: 50%;
        background: $gray;
        @include adaptive(420) {
          content: none;
        }
      }
    }

    p {
      padding-right: 20px;
    }
  }

  &__step-number {
    display: flex;
    align-items: flex-end;
    font-family: 'Oswald', sans-serif;
    font-weight: 500;
    font-size: 64px;
    line-height: 100%;
    letter-spacing: .1em;
    text-transform: uppercase;
    color: $orange;
    margin-bottom: $level-6;

    @include adaptive(600) {
      font-size: 50px;
      margin-bottom: $level-8;
    }

    span {
      font-size: 14px;
      line-height: 2;
      top: 3px;
      letter-spacing: 1px;
    }
  }
}
