.search-page {
  &__form {
    display: flex;
    max-width: 600px;
    width: 100%;
    margin-bottom: 50px;
  }

  &__form-wrapper {
    display: flex;
    width: 100%;
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    padding: 0;
    border: none;
    width: 48px;
    cursor: pointer;

    &:hover {
      &::before {
        color: $orange;
      }
    }

    &::before {
      font-size: 20px;
      transition: .4s;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    margin-bottom: 60px;
  }

  &__item {
    padding: 20px 0;
    border-top: 1px solid $gray-2;

    &:last-child {
      border-bottom: 1px solid $gray-2;
    }

    a {
      color: $orange;
    }

    p {
      margin: 10px 0 0;
    }
  }
}
