.works-page {
  .page__header {
    margin-bottom: 12px;
  }
}

.our-works {
  &__filter {
    display: flex;
    flex-wrap: wrap;
    margin: $level-4 0;
    @include adaptive(1024) {
      margin: $level-6 0;
    }
  }

  &__filter-item {
    &:not(:last-child) {
      margin-right: 48px;

      @include adaptive(1200) {
        margin-right: 40px;
      }

      @include adaptive(768) {
        margin-right: 20px;
      }
    }

    input {
      position: absolute;
      z-index: -1;
      opacity: 0;

      &:checked + .our-works__filter-label {
        color: $orange;
      }
    }
  }

  &__filter-label {
    font-size: 16px;
    color: $gray-2;
    cursor: pointer;

    @include adaptive(1024) {
      font-size: 14px;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 30px);
    margin-right: -30px;

    @include adaptive(420) {
      width: 100%;
      margin-right: 0;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    width: calc(100% / 3 - 30px);
    background: #f2f2f2;
    cursor: pointer;
    margin: 0 30px 30px 0;
    border: 1px solid transparent;
    transition: .4s;

    @include adaptive(768) {
      width: calc(100% / 2 - 30px);
    }

    @include adaptive(420) {
      width: 100%;
      margin: 0 0 30px 0;
    }

    &:hover {
      background: transparent;
      border: 1px solid $gray;
      box-shadow: 5px 10px 20px rgba(0, 0, 0, .05);
    }
  }

  &__item-image {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    width: 100%;
    max-height: 375px;
    height: 100%;

    @include adaptive(600) {
      max-height: 200px;
    }

    img {
      display: block;
      min-width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__item-bottom {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 32px 40px;
    z-index: 2;
    overflow: hidden;
    @include adaptive(1024) {
      padding: 25px 20px;
    }
  }

  &__item-title {
    margin-bottom: 32px;
    @include adaptive(600) {
      font-size: 14px;
    }
  }

  &__link {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
  }
}
