.about-page {
  &__top {
    display: flex;
    width: 100%;
    margin-bottom: 64px;

    @include adaptive(768) {
      flex-direction: column;
    }
  }

  &__director {
    display: flex;
    flex-direction: column;
    max-width: 625px;
    width: 100%;

    @include adaptive(768) {
      max-width: 500px;
      margin: 0 auto 60px;
    }

    &-name {
      span {
        color: $gray-2;
      }

      p {
        margin: 5px 0 0;
        font-weight: bold;
        font-size: 16px;
      }
    }
  }

  &__top-background {
    font-family: "Oswald", sans-serif;
    writing-mode: vertical-rl;
    font-weight: 500;
    font-size: 86px;
    line-height: 100%;
    position: absolute;
    bottom: 0;
    right: 207px;
    letter-spacing: .1em;
    text-transform: uppercase;
    color: #f2f2f2;
    z-index: 4;
    transform: rotate(180deg);
    pointer-events: none;

    @include adaptive(768) {
      right: 0;
      font-size: 60px;
    }
  }

  &__image {
    width: 100%;
    margin-bottom: 115px;
    img {
      z-index: 2;
    }
    &::before {
      content: '';
      position: absolute;
      top: -40px;
      right: -40px;
      width: 80px;
      height: 80px;
      background: $orange;
      opacity: .4;
      z-index: 1;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: -76px;
      left: -76px;
      width: 152px;
      height: 152px;
      background: $orange;
      opacity: .6;
      z-index: 3;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-left: 125px;

    @include adaptive(1200) {
      padding-left: 50px;
    }

    @include adaptive(768) {
      padding-left: 0;
    }
  }

  h2 {
    margin-top: 0;
  }

  &__services {
    display: flex;
    flex-direction: column;

    &-item {
      display: flex;
      flex-direction: column;

      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }

    &-text {
      display: flex;
      align-items: center;

      img {
        width: 32px;
        height: 32px;
        margin-right: 24px;
      }

      p {
        margin: 0;
        font-weight: bold;
        font-size: 16px;
        line-height: 150%;
      }
    }

    ul {
      margin: 12px 0 0 56px;

      li {
        &:not(:last-child) {
          margin-bottom: 0;
        }
      }
    }
  }

  &__content-text {
    padding: 64px 0 39px;
    margin-top: 64px;

    @include adaptive(768) {
      padding: 32px 0 12px;
      margin-top: 32px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -250px;
      right: -225px;
      bottom: 0;
      background: #f2f2f2;

      @include adaptive(768) {
        left: -25px;
        right: -25px;
      }
    }

    &:after {
      content: '';
      position: absolute;
      right: -301px;
      bottom: -76px;
      width: 152px;
      height: 152px;
      background: $orange;
      opacity: .4;
      @include adaptive(768) {
        content: none;
      }
    }
  }
}
