@import "../../helpers/variables";
@import "../../helpers/mixins";

.feedback {
  margin-top: $level-2;
  @include adaptive(1366) {
    margin-top: $level-3;
  }
  @include adaptive(768) {
    display: flex;
    flex-direction: column;
  }
  &__container {
    pointer-events: none;
    overflow: hidden;
  }
  &__wrapper {
    width: calc(50% - 15px);
    height: 574px;
    background: $orange;
    padding: $level-3 96px;
    pointer-events: initial;
    @include adaptive(1600) {
      padding: $level-4 80px;
      width: calc(50% - 10px);
      height: 480px;
    }
    @include adaptive(1200) {
      width: calc(50% - 5px);
      height: 499px;
      padding: $level-4 70px;
    }
    @include adaptive(768) {
      width: 100%;
      height: auto;
      padding: $level-5;
      order: 1;
    }
  }
  &__form {
    overflow: hidden;
    &_active {
      .feedback__form_header {
        transition-delay: 1s;
        transform: translateX(0);
        opacity: 1;
      }

      .feedback__form_text {
        transition-delay: 1.3s;
        transform: translateX(0);
        opacity: 1;

      }

      .feedback__form_fields{
        transition-delay: 1.6s;
        transform: translateX(0);
        opacity: 1;
      }

      .feedback__form_policy {
        transition-delay: 1.9s;
        transform: translateX(0);
        opacity: 1;
      }

      .feedback__form_button{
        transition-delay: 2.2s;
        transform: translateX(0);
        opacity: 1;
      }
    }

    &_header {
      margin-bottom: 20px;
      transform: translateX(-120%);
      transition: 1s;
    }
    &_text {
      max-width: 470px;
      font-size: 16px;
      line-height: 150%;
      width: 100%;
      margin-bottom: 48px;
      transform: translateX(-120%);
      transition: 1s;
      @include adaptive(600) {
        font-size: 14px;
      }
    }
    &_fields {
      transform: translateX(-120%);
      transition: 1s;
    }

    &_policy {
      transform: translateX(-120%);
      transition: 1s;
    }

    &_button {
      width: 220px;
      transform: translateX(-120%);
      transition: 1s;
    }
  }
  &__map {
    width: 100%;
    height: 762px;
    position: absolute;
    bottom: 0;
    display: block;
    @include adaptive(1600) {
      height: 660px;
    }
    @include adaptive(1366) {
      height: 620px;
    }
    @include adaptive(1024) {
      height: 640px;
    }
    @include adaptive(768) {
      position: relative;
      height: 445px;
      order: 2;
    }
    @include adaptive(600) {
      height: 300px;
    }
  }
}
