.popup-parameters {
  width: 100%;
  max-width: 100vw;
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 140;
  &__overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(26, 26, 26, .4);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__body {
    width: 100%;
    height: auto;
    overflow: auto;
    max-width: 807px;
    background-color: #fff;
    -webkit-overflow-scrolling: touch;
    max-height: 100vh;
    @include adaptive(768) {
      max-width: none;
      max-height: none;
      height: 100%;
      display: flex;
      flex-direction: column;
    }
  }
  &__wrap {
    padding: 60px;
    flex-shrink: 0;

    @include adaptive(768){
      min-height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    @include adaptive(600){
      padding: 40px;
    }
  }

  &__wrap-top{
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 100%;
    margin: 0 auto 30px;
  }

  &__title {
    font-family: 'Oswald', sans-serif;
    font-weight: normal;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: .1em;
    text-transform: uppercase;
    color: $gray-2;
    margin-bottom: 15px;

    @include adaptive(600) {
      font-size: 16px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 0;
    border-bottom: 1px solid $gray;
    font-weight: normal;
    font-size: 14px;
    line-height: 160%;
    color: $gray-2;
    @include adaptive(600) {
      font-size: 12px;
    }

    span{
      margin-left: 20px;
      color: black;
    }
  }
}
