.card {
  display: flex;
  flex-direction: column;
  width: 100%;

  &__top {
    display: flex;
    margin-bottom: $level-3;

    @include adaptive(768) {
      flex-direction: column;
    }
  }

  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-basis: 54%;
    max-width: 595px;
    overflow: hidden;

    @include adaptive(768) {
      flex-basis: 100%;
      margin: 0 auto 32px;
    }

    &-brand {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      max-height: 80px;
      height: 100%;
      @include adaptive(420) {
        max-height: 40px;
      }
    }
    &-general {
      display: block;
      height: 100%;
      object-fit: cover;
      z-index: 1;

      @include adaptive(768) {
        height: auto;
        object-fit: contain;
      }
    }
  }

  &__specifications {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-basis: 46%;
    padding-left: 64px;

    @include adaptive(1200) {
      padding-left: 32px;
    }

    @include adaptive(768) {
      flex-basis: 100%;
      padding-left: 0;
    }

    p {
      margin: 0;
    }

    .button {
      margin-top: 46px;
      height: 64px;

      @include adaptive(768) {
        margin-top: 16px;
      }
    }
  }

  &__specifications-title {
    color: $gray-2;
    margin-bottom: $level-6;
  }

  &__specifications-list {
    display: flex;
    flex-direction: column;
    margin: 0 0 40px 0;
    padding: 0;
    list-style: none;
    width: 100%;
  }

  &__specifications-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: normal;
    font-size: 14px;
    line-height: 160%;
    color: $gray-2;
    padding: $level-8 0;

    &:not(:last-child) {
      border-bottom: 1px solid $gray;
    }

    p {
      margin: 0;
    }

    span {
      color: black;
      margin-left: 20px;
      flex: 1 0 25%;

      @include adaptive(1600) {
        padding-right: 0;
      }
    }
  }

  &__tabs {
    display: flex;
    flex-direction: column;
  }

  &-tabs {
    &__links{
      display: flex;
      flex-wrap: wrap;
      margin: $level-5 0 24px;
      padding: 0;
      list-style: none;
    }

    &__links-item {
      font-size: 16px;
      font-weight: normal;
      color: $gray-2;
      margin-right: 47px;
      margin-bottom: $level-6;
      @include adaptive(768) {
        font-size: 14px;
        margin-right: 20px;
        margin-bottom: $level-7;
      }

      &.ui-tabs-active,
      &:hover {
        color: $orange;
      }

      a {
        color: inherit;
        font-size: inherit;
        text-decoration: none;
        font-weight: inherit;
      }
    }
  }

  &__tab {
    &-list {
      padding: $level-3 0 $level-3 80px;
      background: #f2f2f2;
      margin-bottom: $level-4;

      @include adaptive(1200) {
        padding: $level-3 0 $level-3 40px;
      }

      @include adaptive(420) {
        padding: $level-3 0 $level-3 20px;
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: #f2f2f2;
        transform: translateX(99%);
      }
    }

    &-wrapper {
      display: flex;
      width: 100%;
      margin-top: $level-6;

      @include adaptive(768) {
        flex-direction: column;
      }
    }
  }

  &-tab {
    &__parameters{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      max-width: 297px;
      width: 100%;
      margin-right: 80px;

      @include adaptive(1200) {
        max-width: 230px;
        margin-right: 40px;
      }

      @include adaptive(768) {
        margin-right: 0;
        margin-bottom: 40px;
        max-width: none;
      }
    }

    &__parameters-color {
      width: 100%;
      height: 221px;
      margin-bottom: 30px;

      @include adaptive(768) {
        max-width: 297px;
        height: 200px;
      }

      @include adaptive(420) {
        height: 150px;
      }
    }

    &__parameters-list {
      display: flex;
      flex-direction: column;
      padding: 0;
      margin: 0;
      list-style: none;
      width: 100%;
    }

    &__parameters-item{
      display: flex;
      justify-content: space-between;
      width: 100%;
      padding: 8px 0;

      &:not(:last-child) {
        border-bottom: 1px solid $gray;
      }

      p {
        color: $gray-2;
        margin: 0;
      }

      span {
        margin-left: 20px;
        flex: 1 0 25%;
      }
    }

    &__parameters-gamma {
      margin-top: 16px;
      display: flex;
      align-items: center;
      p {
        padding-left: 12px;
        color: $dark-blue;
        margin: 0;
      }

      span {
        display: flex;
        align-items: center;
        width: 22px;
        height: 12px;

        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          background: $orange;
          width: 12px;
          height: 12px;
          z-index: 1;
          border-radius: 50%;
        }
        &::after {
          content: '';
          position: absolute;
          top: 0;
          right: 0;
          background: $dark-blue;
          width: 12px;
          height: 12px;
          z-index: 2;
          border-radius: 50%;
        }
      }
    }

    &__colors-list{
      display: flex;
      width: calc(100% - 297px - 80px);

      @include adaptive(1200) {
        width: calc(100% - 230px - 40px);
      }

      @include adaptive(768) {
        width: 100%;
      }
    }

    &__colors-wrap{
      display: flex;
      flex-wrap: wrap;
      width: calc(100% + 30px);
      margin-right: -30px;
    }

    &__colors-item {
      display: flex;
      flex-direction: column;
      width: calc(100% / 4 - 30px);
      margin: 0 30px 32px 0;

      @include adaptive(576) {
        width: calc(100% / 3 - 30px);
      }
    }

    &__colors-code {
      width: 100%;
      height: 74px;
      margin-bottom: $level-6;

      @include adaptive(420) {
        height: 40px;
      }
    }

    &__colors-title {
      font-size: 16px;

      @include adaptive(1200) {
        font-size: 14px;
      }
    }
  }

  &__clarification {
    width: 100%;
    padding-top: 24px;
    border-top: 1px solid $gray;

    p {
      margin: 0;
      color: $gray-2;
    }
  }
}
