.sidebar {
  display: flex;
  flex-direction: column;
  max-width: 345px;
  width: 100%;
  margin-right: 30px;
  @include adaptive(1600) {
    max-width: 270px;
  }

  @include adaptive(1024) {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 119;
    background: white;
    max-width: none;
    align-items: center;
    justify-content: flex-start;
    padding: 70px 25px;
    max-height: 100vh;
    overflow: auto;
  }

  &_open {
    @include adaptive(1024) {
      display: flex;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: sticky;
    top: 20px;
  }

  &__item {
    padding-bottom: 16px;
    border-bottom: 1px solid $gray;
    @include adaptive(1024) {
      max-width: 320px;
      width: 100%;
    }

    &:hover {
      color: $orange;

      .sidebar__item-wrap {
        color: $orange;

        span {
          color: $orange;
        }
      }
    }

    &:not(:first-child) {
      padding: 16px 0;
    }

    &_active {
      .sidebar__item-wrap {
        color: $orange;

        span {
          color: $orange;
        }
      }
    }

    &_open {
      .sidebar__level2 {
        display: flex;
      }
    }

    a {
      color: inherit;
      font-weight: inherit;

      &:hover {
        text-decoration: none;
      }
    }

    .icon {
      transform: scale(-1, 1);
      cursor: pointer;
    }
  }

  &__item-wrap {
    display: flex;
    justify-content: space-between;
    font-family: 'Oswald', sans-serif;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: .1em;
    text-transform: uppercase;
    color: black;

    @include adaptive(1600) {
      font-size: 14px;
    }

    span {
      color: $gray-2;
    }

    a {
      margin-right: 15px;
    }
  }

  &__level2 {
    display: none;
    flex-direction: column;
  }

  &__level2-item {
    &_active {
      .sidebar__subnav-wrap {
        color: $orange;

        .icon {
          opacity: 1;
        }
      }
    }

    &_open {
      .sidebar__level3 {
        display: flex;
      }
    }
  }

  &__subnav-wrap {
    display: flex;
    align-items: center;
    font-weight: normal;
    font-size: 14px;
    line-height: 160%;
    margin-top: 12px;
    color: black;
    @include adaptive(1600) {
      font-size: 12px;
    }

    .icon {
      color: $gray-2;
      opacity: 0;
      margin-right: 12px;
    }

    &:hover {
      color: $orange;

      .icon {
        opacity: 1;
      }
    }
  }

  &__level3 {
    display: none;
    flex-direction: column;

    a {
      font-weight: normal;
      font-size: 14px;
      line-height: 160%;
      margin: 4px 0 0 42px;
      color: $gray-2;

      @include adaptive(1600) {
        font-size: 12px;
      }

      &.active,
      &:hover {
        color: black;
      }
    }
  }

  &__close {
    display: none;

    @include adaptive(1024) {
      display: flex;
    }
  }

  &-open {
    display: none;
    @include adaptive(1024) {
      display: flex;
      margin-bottom: 30px;
    }
  }
}
