.works__detail {
  .breadcrumbs {
    padding: 48px 0 12px;
    margin: 0;
  }
  .page__header {
    margin-bottom: 12px;
  }
}

.works-detail {
  display: flex;
  flex-direction: column;

  &__top {
    display: flex;
    min-height: 769px;

    @include adaptive(768) {
      flex-direction: column;
      min-height: unset;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    max-width: 530px;
    width: 100%;
    margin-right: 95px;
    margin-bottom: 100px;

    @include adaptive(1024) {
      margin-right: 40px;
    }

    @include adaptive(768) {
      margin-right: 0;
      margin-bottom: 0;
      max-width: unset;
    }
  }

  &__image {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    margin-right: -225px;
    width: 100%;

    @include adaptive(1200) {
      margin-right: -125px;
    }

    @include adaptive(1024) {
      margin-right: -25px;
    }

    @include adaptive(768) {
      margin-right: 0;
    }

    img {
      display: block;
      min-width: 100%;
      height: 100%;
      object-fit: cover;

      @include adaptive(768) {
        object-fit: contain;
        height: auto;
      }
    }
  }

  &__specifications-list {
    margin: 40px 0 28px;

    @include adaptive(768) {
      margin: 30px 0 20px;
    }
  }
  &__specifications-item {
    span {
      flex: 1 0 45%;
    }
  }

  &__slider-container {
    margin-top: -100px;
    margin-bottom: 32px;
    min-height: 460px;

    @include adaptive(1600) {
      min-height: 400px;
    }

    @include adaptive(1200) {
      min-height: 350px;
    }

    @include adaptive(768) {
      margin-top: 32px;
    }

    &::before {
      content: '';
      position: absolute;
      background: #f2f2f2;
      top: 135px;
      width: 850px;
      left: -225px;
      bottom: -32px;

      @include adaptive(1200) {
        width: 670px;
      }
    }
  }

  &__slider {
    width: 100%;
  }

  &__slider-item {
    display: flex;
    align-items: center;
    justify-content: center;
    height: auto;

    &:hover {
      a::before {
        opacity: .5;
      }
      span {
        opacity: 1;
      }
    }

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: #272727;
        z-index: 1;
        opacity: 0;
        transition: .4s;
      }
    }

    span {
      content: '';
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 64px;
      height: 64px;
      border: 1px solid white;
      border-radius: 50%;
      color: white;
      font-size: 30px;
      z-index: 2;
      opacity: 0;
      transition: .4s;

      &::before {
        content: '';
        position: absolute;
        height: 16px;
        background: white;
        width: 2px;
      }

      &::after {
        content: '';
        position: absolute;
        width: 16px;
        background: white;
        height: 2px;
      }
    }

    img {
      display: block;
      width: 100%;
      object-fit: cover;
      height: 100%;
    }
  }

  &__slider-bottom {
    display: flex;
    justify-content: space-between;

    @include adaptive(768) {
      flex-direction: column;
      justify-content: flex-start;
    }

    p {
      margin: 64px 0 auto 125px;
      max-width: 720px;
      width: 100%;

      @include adaptive(1600) {
        max-width: 460px;
      }

      @include adaptive(1024) {
        margin: 24px 0 0;
      }

      @include adaptive(768) {
        max-width: none;
      }
    }
  }

  &__controls {
    display: flex;
    align-items: flex-start;
    width: 220px;
  }

  &__control {
    top: 0;
    right: 0;
    left: 0;
    margin-top: 48px;
    height: auto;
  }

  &__bg-text {
    font-family: 'Oswald', sans-serif;
    font-weight: 500;
    font-size: 86px;
    line-height: 100%;
    position: absolute;
    bottom: 0;
    letter-spacing: .1em;
    text-transform: uppercase;
    color: $gray;
    left: -10%;

    @include adaptive(1024) {
      left: 0;
      font-size: 60px;
    }

    @include adaptive(420) {
      font-size: 48px;
    }
  }
}
