.catalog-inner{
  &__wrapper {
    display: flex;

    @include adaptive(1024) {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
    width: calc(100% - 345px);

    @include adaptive(1600) {
      width: calc(100% - 270px);
    }

    @include adaptive(1024) {
      width: 100%;
    }
  }

  &__section-top {
    display: flex;
    align-items: center;
    padding: 10px 32px 10px 48px;
    border: 1px solid  $gray;
    margin-bottom: 31px;

    @include adaptive(768) {
      flex-direction: column;
      padding: 10px 20px;
    }

    img {
      width: auto;
      margin-right: 70px;

      @include adaptive(768) {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
  }

  &__section-title{
    font-family: 'Oswald', sans-serif;
    text-align: end;
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: .1em;
    text-transform: uppercase;
    color: $gray-2;
    margin-left: auto;

    @include adaptive(1600) {
      font-size: 14px;
    }

    @include adaptive(768) {
      margin: 0 auto;
      text-align: center;
    }

    @include adaptive(600) {
      font-size: 12px;
    }
  }

  &__preview-list {
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 30px);
    margin-bottom: -31px;

    @include adaptive(576) {
      width: calc(100% + 10px);
      margin-bottom: -10px;
    }

    @include adaptive(360) {
      width: 100%;
    }
  }

  &__preview-item {
    display: flex;
    flex-direction: column;
    border: 1px solid $gray;
    width: calc(100% / 3 - 30px);
    margin-bottom: 31px;
    margin-right: 30px;
    transition: .4s;

    @include adaptive(1600) {
      width: calc(100% / 2 - 31px);
    }

    @include adaptive(1024) {
      width: calc(100% / 3 - 30px);
    }

    @include adaptive(768) {
      width: calc(100% / 2 - 30px);
    }

    @include adaptive(576) {
      width: calc(100% / 2 - 10px);
      margin-right: 10px;
      margin-bottom: 10px;
    }

    @include adaptive(360) {
      width: 100%;
      margin-right: 0;
    }

    &:hover {
      box-shadow: 5px 10px 20px rgba(0, 0, 0, .05);
    }
  }

  &__preview-image {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-height: 225px;
    height: 100%;
    margin-bottom: $level-6;

    @include adaptive(576) {
      max-height: 160px;
    }

    img {
      display: block;
      height: 100%;
      object-fit: cover;
    }

    a {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
    }
  }

  &__preview-title {
    padding: 0 32px;

    @include adaptive(576) {
      font-size: 13px;
      padding: 0 16px;
    }
  }

  &__preview-buttons {
    display: flex;
    padding-top: 32px;
    margin-top: auto;

    @include adaptive(576) {
      flex-direction: column;
      padding-top: 16px;
    }

    & .button {
      height: 64px;
      width: 50%;
      min-width: auto;

      @include adaptive(576) {
        width: 100%;
        height: 44px;
      }

      &:nth-child(1) {
        &:hover {
          background: black;
          color: white;
        }
      }
      &:nth-child(2) {
        &:hover {
          color: $orange;
        }
      }
    }
  }
}
