.services-page {
  .page__header {
    margin-bottom: 12px;
  }
}

.services-inner {
  &__wrapper{
    margin: $level-4 0 $level-3;
  }

  &__item {
    height: auto;

    @include adaptive(1024) {
      width: 50%;
    }
    @include adaptive(576) {
      width: 100%;
    }

    img {
      position: absolute;
    }
  }

  &__item-info {
    position: relative;
    justify-content: flex-start;
    padding: $level-5  $level-7  $level-5  $level-5;
  }

  &__item-name {
    margin-top: $level-4;
  }

  &__links {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    padding: 0;
    margin: $level-6 0;
  }

  &__links-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: white;

    &:hover {
      span.icon {
        opacity: 1;
      }

      color: $orange;
    }

    &:not(:last-child) {
      margin-bottom: $level-7;
    }

    a {
      color: inherit;
      font-size: inherit;
      text-decoration: none;
      font-weight: inherit;
    }
    span.icon {
      opacity: 0;
      margin-left: 8px;
      transform: scale(-1, 1);
    }
  }
}
