.certificates {
  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;

    @include adaptive(768) {
      flex-direction: column;
    }
  }

  &__controls {
    display: flex;

    @include adaptive(768) {
      top: 30px;
    }

    .button-control-next{
      margin-right: 0;
    }
  }

  &__header {
    margin-bottom: 0;

    @include adaptive(768) {
      margin-bottom: 30px;
    }
  }

  &__slider-item {
    display: flex;
    flex-direction: column;
    height: auto;
  }

  &__slider-image {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    height: auto;

    img {
      display: block;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
    a {
      height: 100%;
      width: 100%;
    }
  }
}
