.news {
  margin-bottom: 64px;
  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;

    @include adaptive(768) {
      flex-direction: column;
    }
  }

  &__controls {
    display: flex;

    @include adaptive(768) {
      top: 30px;
    }

    .button-control-next{
      margin-right: 0;
    }
  }

  &__header {
    margin-bottom: 0;

    @include adaptive(768) {
      margin-bottom: 30px;
    }
  }

  &__slider-item {
    display: flex;
    flex-direction: column;
    height: auto;
  }

  &__slider-image {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    max-height: 294px;
    height: auto;

    img {
      display: block;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
    a {
      height: 100%;
      width: 100%;
    }
  }

  &__slider-info {
    display: flex;
    flex-direction: column;
    padding: 33px 40px;
    background: #f2f2f2;
    flex: 1;

    @include adaptive(768) {
      padding: 32px 20px;
    }

    p {
      margin: 0 0 32px;
    }
  }

  &__slider-title {
    max-width: 302px;
    width: 100%;
    margin-bottom: 12px;
  }

  &__slider-link {
    margin-top: auto;
  }
}
