.form-advice {
  padding: $level-3 0;
  margin: $level-3 0 $level-2;
  background: $orange;

  &__wrapper {
    width: 100%;
    padding: 0 125px 0 95px;
    @include adaptive(1024) {
      padding: 0 60px;
    }

    @include adaptive(420) {
      padding: 0 30px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__title {
    margin-bottom: 20px;

    @include adaptive(600) {
      font-size: 20px;
    }
  }

  &__text {
    font-weight: normal;
    font-size: 16px;
    line-height: 150%;
    margin-bottom: 15px;
  }

  &__fields {
    display: flex;
    align-items: flex-end;
    margin-bottom: $level-5;

    @include adaptive(768) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__label {
    max-width: 440px;
    width: 100%;
    margin-right: 60px;
    @include adaptive(768) {
      max-width: none;
      margin-right: 0;
      margin-bottom: 30px;
    }
  }

  &__button {
    max-width: 220px;
    width: 100%;
    margin-left: auto;
  }

  &__policy{
    margin-bottom: 0;
  }

}
