.pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 48px 0 64px;
  @include adaptive(600) {
    margin: 24px 0 32px;
  }
  &__item{
    margin-right: 15px;
    text-decoration: none;
    transition: all .2s ease-in-out;
    width: 26px;
    height: 28px;
    &:last-child{
      margin-right: 0;
    }

    &_number{
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: 'Oswald', sans-serif;
      font-weight: normal;
      font-size: 14px;
      line-height: 110%;
      text-align: center;
      letter-spacing: .1em;
      text-transform: uppercase;
      text-decoration: none;
      color: black;

      &:hover,
      &.pagination__item_active{
        color: $orange;
        background: #f2f2f2;
        text-decoration: none;
      }
    }

    &_next{
      padding: 0;
      margin-left: 0;
      span.icon {
        color: black;
        position: relative;
      }
    }
    &_prev{
      padding: 0;
      margin-right: 16px;
      span.icon {
        color: black;
        position: relative;
        transform: translateY(-50%) scale(1, -1);
      }
    }
  }
}
