.contacts {
  &__map {
    height: 400px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 40px;
  }

  &__item {
    display: block;
    margin-bottom: 15px;
    width: 25%;
    padding-right: 30px;

    @include adaptive(1024) {
      width: 50%;
    }

    @include adaptive(576) {
      width: 100%;
    }

    p {
      margin: 0 0 5px;
      font-weight: bold;
    }

    a {
      color: black;
      font-weight: normal;
    }
  }
}
